"use client";
import { useEffect, useState } from "react";

export default function SettingsPage() {
  const [prefs, setPrefs] = useState({ theme: "light", notifications: true });

  const fetchPrefs = async () => {
    const res = await fetch("/api/preferences");
    const data = await res.json();
    setPrefs(data);
  };

  const savePrefs = async () => {
    await fetch("/api/preferences", {
      method: "POST",
      headers: {"Content-Type": "application/json"},
      body: JSON.stringify(prefs)
    });
    alert("Preferências salvas!");
  };

  useEffect(()=>{ fetchPrefs(); }, []);

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">⚙️ Configurações</h1>
      <label>Tema:</label>
      <select value={prefs.theme} onChange={e=>setPrefs({...prefs, theme:e.target.value})} className="border p-2 mb-2">
        <option value="light">Claro</option>
        <option value="dark">Escuro</option>
      </select>
      <div>
        <label>
          <input 
            type="checkbox" 
            checked={prefs.notifications} 
            onChange={e=>setPrefs({...prefs, notifications:e.target.checked})} 
          />
          Notificações
        </label>
      </div>
      <button onClick={savePrefs} className="bg-green-500 text-white py-2 px-4 mt-2 rounded">Salvar</button>
    </div>
  );
}
